import axios from "axios";

// const URL = "https://particle-io.herokuapp.com/api/particle";
const URL = "api/particle";

class TempService {
  // Get temperaties from the particle API

  static getTemperature() {
    return new Promise((resolve, reject) => {
      return axios
        .get(URL)
        .then((res) => {
          resolve(res.data);
        })
        .catch((e) => reject(e));
    });
  }
}

export default TempService;
