<template>
  <div class="app-container">
    <the-header></the-header>
    <h3 class="sub-title">Statistics Based on the Last 8 Hours of Data</h3>
    <cards :temperatures="temperatures"></cards>
    <div class="actions">
      <button class="btn" @click="getTemperatures">Fetch Temperature</button>
    </div>
  </div>
</template>

<script>
import TheHeader from "./components/TheHeader.vue";
import Cards from "./components/Cards.vue";
import Service from "./TemperatureService.js";

export default {
  name: "App",
  components: {
    TheHeader,
    Cards,
  },

  data() {
    return {
      temps: [],
      temperatures: {
        min: {
          value: NaN,
          timestamp: null,
        },
        last: {
          value: NaN,
          timestamp: null,
        },
        max: {
          value: NaN,
          timestamp: null,
        },
      },
    };
  },

  methods: {
    async getTemperatures() {
      this.temps = await Service.getTemperature();

      const minTemp = this.temps.reduce((previous, current) => {
        return previous.data < current.data ? previous : current;
      }, 0);

      const maxTemp = this.temps.reduce((previous, current) => {
        return previous.data > current.data ? previous : current;
      }, 0);

      this.temperatures = {
        min: {
          value: minTemp.data,
          timestamp: minTemp.publishedAt,
        },
        max: {
          value: maxTemp.data,
          timestamp: maxTemp.publishedAt,
        },
        last: {
          value: this.temps[0].data,
          timestamp: this.temps[0].publishedAt,
        },
      };
    },
  },

  created() {
    this.getTemperatures();
  },
};
</script>

