<template>
  <div class="card-wrapper">
    <h3>{{ title }}</h3>
    <div class="card-body">
      <p>
        Temperature = <span>{{ value }} Â°F</span>
      </p>
    </div>
    <div class="card-footer">
      <p class="timestamp">Published at: {{ publishedAt }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "TheCard",

  props: ["title", "value", "timestamp"],

  computed: {
    publishedAt() {
      const millis = parseInt(this.timestamp);

      const date = new Date(millis);

      return `${
        date.getMonth() + 1
      }/${date.getDate()}/${date.getFullYear()} - ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
    },
  },
};
</script>

